import React from 'react'
import ToDoneItems from './ToDoneItems'

// this is parent

export default function ToDonesContainer(props) {
   

    return (
        <div className="todones-container">
            <h3>BACKLOG</h3>
            {props.todones.map(todone=>{
                  return (
                   <ToDoneItems key={todone.id} todone = {todone} updateTask={props.updateTask} deleteTask={props.deleteTask}/>
                )
            })}
            
        </div>
    )
}
