
import React from 'react';
import Navigation from './components/Navigation';
import "./css/App.scss";
import ToDosContainer from './components/ToDosContainer';
import ToDonesContainer from './components/ToDonesContainer';
import { render } from '@testing-library/react';
import {v4 as uuid} from "uuid"


class App extends React.Component {
  state={
    todoList:
   [ 
    // {text:"start react project", done:false},
    // {text:"call mom", done:true},
    // {text:"hang laundry", done:true},
    // {text:"live Coding", done:true},
    // {text:"Play Catan", done:false},
    // {text:"clean kitchen", done:true}
  ]
}

addTask=(todotext)=>{
  let task={id:uuid(),text:todotext,done:false}
  this.setState({
    todoList: [...this.state.todoList, task]
  })
}

updateTask=(id)=>{
  let updatedItems=this.state.todoList.map(item=>{
    if(item.id===id){ // does the id matches to the id I am receiving?
      item.done=!item.done; //item.done?false:true;
      return item
    }else{
      return item
    }
  })
  this.setState({todoList:updatedItems})
}


deleteTask=(id)=>{
  let updatedApp = this.state.todoList.filter(item=>item.id!==id)
  this.setState({todoList:updatedApp})
}

    
render(){
  let todos=this.state.todoList.filter(item=>!item.done) //[] if item is false return that value, (item.done ===false)
  let todones=this.state.todoList.filter(item=>item.done ) // item.done === true is same

  return (
    <div className="app">
      <Navigation/>
      <ToDosContainer todos = {todos} addTask={this.addTask} updateTask={this.updateTask} />
      <ToDonesContainer todones = {todones} updateTask={this.updateTask} deleteTask={this.deleteTask}/>
    </div>
  );
}
}
 





export default App;

